*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: system-ui;
  background-color: rgb(252, 252, 252);

  margin: 0;
}

main {
  max-width: min(1200px, 100% - 4rem);
  margin: 0 auto;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

form div {
  margin-left: 2rem;

  display: grid;
  grid-template-columns: auto 1fr;
  gap: 1rem;
}

form input {
  max-width: max-content;
}

li {
  list-style: none;
}

#cards {
  margin-top: 1rem;

  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.card {
  background-color: #ebebeb;

  border: 2px solid #b6b6b6;
  border-radius: 5px;
  box-shadow: 2px 2px 4px #c0c0c0;

  padding: 1rem 1rem;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 0.5rem;
}

.card div:first-of-type {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 0.5rem 1rem;
}

.capitalise-first::first-letter {
  text-transform: capitalize;
}

.card div:first-child span:nth-child(2n-1) {
  font-weight: bold;
}

.card span {
  overflow: hidden;
  text-overflow: ellipsis;
}

.button {
  color: black;
  font-weight: bold;
  border-radius: 3px;
  box-shadow: 2px 2px 4px #d8d8d8;


  padding: 0.5rem;

  cursor: pointer;
}

#new-book {
  margin-bottom: 1rem;
  text-decoration: none;
  background-color: #add8e6;
  border: 2px solid #617981;
}

#new-book:hover {
  background-color: #97bdca;
}

#new-book:active {
  background-color: #84a6b1;
}

#save-book {
  margin-top: 1rem;
  background-color: #90ee90;
  border: 2px solid #508650;
}

#save-book:hover {
  background-color: #7bce7b;
}

#save-book:active {
  background-color: #6cb46c;
}

#update-book {
  margin-top: 1rem;
  background-color: #c890ee;
  border: 2px solid #694c7c;
}

#update-book:hover {
  background-color: #ac7ccc;
}

#update-book:active {
  background-color: #936aaf;
}

#cancel {
  margin-top: 1rem;
  margin-bottom: 1rem;
  background-color: #ff8170;
  border: 2px solid #8d463d;
}

#cancel:hover {
  background-color: #e27162;
}

#cancel:active {
  background-color: #c26154;
}

.card-buttons {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.5rem;
}

.toggle-read {
  background-color: rgb(159, 201, 255);
  border: 2px solid rgb(127, 161, 206);
}

.toggle-read:hover {
  background-color: rgb(142, 181, 231);
}

.toggle-read:active {
  background-color: rgb(134, 172, 221);
}

.edit {
  background-color: rgb(255, 210, 159);
  border: 2px solid rgb(199, 163, 122);
}

.edit:hover {
  background-color: rgb(228, 185, 137);
}

.edit:active {
  background-color: rgb(214, 173, 126);
}

.delete {
  background-color: rgb(255, 159, 159);
  border: 2px solid rgb(202, 125, 125);
}

.delete:hover {
  background-color: rgb(231, 131, 131);
}

.delete:active {
  background-color: rgb(223, 126, 126);
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;;;EAGE,sBAAsB;AACxB;;AAEA;EACE,sBAAsB;EACtB,oCAAoC;;EAEpC,SAAS;AACX;;AAEA;EACE,mCAAmC;EACnC,cAAc;EACd,gBAAgB;EAChB,mBAAmB;AACrB;;AAEA;EACE,iBAAiB;;EAEjB,aAAa;EACb,+BAA+B;EAC/B,SAAS;AACX;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;;EAEhB,aAAa;EACb,4DAA4D;EAC5D,SAAS;AACX;;AAEA;EACE,yBAAyB;;EAEzB,yBAAyB;EACzB,kBAAkB;EAClB,+BAA+B;;EAE/B,kBAAkB;;EAElB,aAAa;EACb,sBAAsB;EACtB,2BAA2B;EAC3B,WAAW;AACb;;AAEA;EACE,aAAa;EACb,+BAA+B;EAC/B,gBAAgB;AAClB;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,gBAAgB;EAChB,uBAAuB;AACzB;;AAEA;EACE,YAAY;EACZ,iBAAiB;EACjB,kBAAkB;EAClB,+BAA+B;;;EAG/B,eAAe;;EAEf,eAAe;AACjB;;AAEA;EACE,mBAAmB;EACnB,qBAAqB;EACrB,yBAAyB;EACzB,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,gBAAgB;EAChB,yBAAyB;EACzB,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,gBAAgB;EAChB,yBAAyB;EACzB,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,gBAAgB;EAChB,mBAAmB;EACnB,yBAAyB;EACzB,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,qCAAqC;EACrC,WAAW;AACb;;AAEA;EACE,oCAAoC;EACpC,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;EACpC,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;EACpC,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;AACtC","sourcesContent":["*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\nbody {\n  font-family: system-ui;\n  background-color: rgb(252, 252, 252);\n\n  margin: 0;\n}\n\nmain {\n  max-width: min(1200px, 100% - 4rem);\n  margin: 0 auto;\n  margin-top: 2rem;\n  margin-bottom: 2rem;\n}\n\nform div {\n  margin-left: 2rem;\n\n  display: grid;\n  grid-template-columns: auto 1fr;\n  gap: 1rem;\n}\n\nform input {\n  max-width: max-content;\n}\n\nli {\n  list-style: none;\n}\n\n#cards {\n  margin-top: 1rem;\n\n  display: grid;\n  grid-template-columns: repea